html {
  font-family: 'Comic Sans MS', sans-serif;
  font-size: 16px;
}

body {
  background-color: #eadbcb;
  margin: 0;
}

input, button {
  background-color: #F0F0F0;
  border: none;
  color: #333;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  font-weight: bold;
  transition: background-color 0.3s ease-in-out;
  padding: 10px 20px;
  margin: 5px;
}

button {
  border-color: black;
  border-style: ridge;
  border-width: 3px;
}

input:hover, button:hover {
  background-color: #E0E0E0;
}

input:active, button:active {
  background-color: #D0D0D0;
}

svg {
  display: none;
}

.board-container {
  background-color: grey;
  border: 5px solid grey;
  gap: 5px;
  display: grid;
  grid-template: repeat(3, 1fr) / repeat(3, 1fr);
  height: 50vh;
  width: 50vh;
}

#welcome-screen {
    display: grid;
    grid-template-rows: 1fr 1fr 30px;
    margin-top: 20vh;
    height: 30vh;
    align-items: center;
    justify-content: center;
    justify-items: center;
}

.welcome-screen__submit-names {
  width: 50%;
}

#game-elements {
  height: 95vh;

  display: grid;
  grid-template-rows: 30px min-content min-content 30px;
  justify-content: center;
  align-content: center;
  align-items: center;
  justify-items: center;
}

.cell {
  background-color: white;
  display: flex;
  padding: 25%;
}

.cell.o > .circle {
  display: block;
  fill: blue;
}

.cell.x > .cross {
  display: block;
  fill: red;
}
