html {
  font-family: Comic Sans MS, sans-serif;
  font-size: 16px;
}

body {
  background-color: #eadbcb;
  margin: 0;
}

input, button {
  color: #333;
  background-color: #f0f0f0;
  border: none;
  border-radius: 8px;
  margin: 5px;
  padding: 10px 20px;
  font-weight: bold;
  transition: background-color .3s ease-in-out;
  box-shadow: 0 0 10px #0000001a;
}

button {
  border: 3px ridge #000;
}

input:hover, button:hover {
  background-color: #e0e0e0;
}

input:active, button:active {
  background-color: #d0d0d0;
}

svg {
  display: none;
}

.board-container {
  background-color: gray;
  border: 5px solid gray;
  grid-template: repeat(3, 1fr) / repeat(3, 1fr);
  gap: 5px;
  width: 50vh;
  height: 50vh;
  display: grid;
}

#welcome-screen {
  grid-template-rows: 1fr 1fr 30px;
  justify-content: center;
  place-items: center;
  height: 30vh;
  margin-top: 20vh;
  display: grid;
}

.welcome-screen__submit-names {
  width: 50%;
}

#game-elements {
  grid-template-rows: 30px min-content min-content 30px;
  place-content: center;
  place-items: center;
  height: 95vh;
  display: grid;
}

.cell {
  background-color: #fff;
  padding: 25%;
  display: flex;
}

.cell.o > .circle {
  fill: #00f;
  display: block;
}

.cell.x > .cross {
  fill: red;
  display: block;
}

